<template>
    <div class="wardrobe-elements">
        <div class="wardrobe-element actions">
            <h3>Handlinger</h3>
            <div class="wardrobe-buttons">
                <button @click="SaveOutfit()">Gem Outfit</button>
                <button @click="ResetOutfit()">Reset Outfit</button>
            </div>
        </div>
        <div v-for="(outfit, index) in sortedWardrobe" :key="outfit.index" :class="index === (Wardrobe.length - 1) ? 'wardrobe-element end' : 'wardrobe-element'">
            <h3>{{outfit.label}}</h3>
            <div class="wardrobe-buttons">
                <button @click="ChooseOutfit(outfit)">Vælg Outfit</button>
                <button @click="OverrideOutfit(outfit)">Override Outfit</button>
                <button @click="DeleteOutfit(outfit)">Slet Outfit</button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2'

export default {
    props: {
        Wardrobe: {},
        selectedMenu: {},
        NameResource: {},
        close: {},
    },

    methods: {
        SaveOutfit: function() {
            (async () => {
                const { 
                    value: text 
                } = await Swal.fire({
                    title: 'Navn på dit Outfit',
                    input: 'text',
                    inputPlaceholder: 'Skriv navnet på dit Outfit her...',
                    showCancelButton: true
                })
                
                if (text) {
                    let name = text;
                    
                    const NameResource = this.NameResource[0];
                    axios.post(`https://${NameResource}/SaveOutfit`, { 
                        outfit_name: name 
                    }).catch(function (error) {
                        console.log(error + ' : Error at SaveOutfit');
                    });
                }
            })()
        },

        ResetOutfit: function() {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/ResetOutfit`).catch(function (error) {
                console.log(error + ' : Error at ResetOutfit');
            });
        },

        ChooseOutfit: function(outfit) {
            let index = outfit.index;

            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/ChooseOutfit`, {
                index: index
            }).catch(function (error) {
                console.log(error + ' : Error at ChooseOutfit');
            });
        },

        OverrideOutfit: function(outfit) {
            let index = outfit.index;

            Swal.fire({
                title: 'Er du sikker på at overskrive dette outfit?',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Nej`,
            }).then((result) => {
                if (result.isConfirmed) {
                    const NameResource = this.NameResource[0];
                    axios.post(`https://${NameResource}/OverrideOutfit`, {
                        index: index
                    }).catch(function (error) {
                        console.log(error + ' : Error at OverrideOutfit');
                    });
                }
            })
        },

        DeleteOutfit: function(outfit) {
            let index = outfit.index;

            Swal.fire({
                title: 'Er du sikker på at slette dette outfit?',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Nej`,
            }).then((result) => {
                if (result.isConfirmed) {
                    const NameResource = this.NameResource[0];
                    axios.post(`https://${NameResource}/DeleteOutfit`, {
                        index: index
                    }).catch(function (error) {
                        console.log(error + ' : Error at DeleteOutfit');
                    });
                }
            })
        },
    },

    computed: {
        sortedWardrobe: function() {
            let dressings = [];

            for (let key in this.Wardrobe) {
                let dressing = this.Wardrobe[key];
                dressings.push({index: key, label: dressing.label})
            }

            return dressings.sort((a, b) => (a.label > b.label) ? 1 : -1);
        },
    },
}
</script>

<style>
.wardrobe-elements {
    display: block;
    max-height: calc(100vh - 200px);
    overflow: auto;
}

.wardrobe-element {
    display: flex;
    flex-flow: column;
    background-color: #575757;
    margin: 0px 10px 20px 10px;
}

.wardrobe-element.end {
    margin-bottom: 0;
}

.wardrobe-element h3 {
    background-color: #424242;
    padding: 8px;
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 20px;
}

.wardrobe-element .wardrobe-buttons {
    display: flex;
    padding: 8px;
}

.wardrobe-element .wardrobe-buttons button {
    flex: 1;
    margin: 0px 8px;
    outline: none;
    border: none;
    color: #e7e7e7;
    text-transform: uppercase;
    font-weight: bold;
    background-color: #c32222;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wardrobe-element .wardrobe-buttons button:hover {
    background-color: #ae1e1e;
}

.wardrobe-elements::-webkit-scrollbar {
    width: 0.5em;
}

.wardrobe-elements::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.wardrobe-elements::-webkit-scrollbar-thumb {
  background-color: #ae1e1e;
  outline: 1px solid #313131;
  border-radius: 5px;
}
</style>