<template>
    <div id="App" v-if="opened">
        <div class="freeroam-container" v-if="isFreeroaming">
            <div class="freeroam-wrapper">
                <h1>Klik på N for at fjerne Freeroam</h1>
            </div>
        </div>
        <div class="rotate-container" v-if="!isFreeroaming">
            <div class="rotate-wrapper">
                <button @click="RotateCamera('left')"><i class="fas fa-angle-double-left"></i></button>
                <button @click="ToggleFreeRoam(true)" class="middle"><i class="fas fa-video"></i></button>
                <button @click="RotateCamera('right')"><i class="fas fa-angle-double-right"></i></button>
            </div>
        </div>
        <div class="clothing">
            <div class="clothing-container">
                <div class="clothing-header" v-if="!isTattoShop">
                    <button v-if="hasStoreItems('body')" @click="selectedMenu = 'body'" :class="selectedMenu === 'body' ? 'clothing-button active' : 'clothing-button'">Ansigt</button>
                    <button v-if="hasStoreItems('clothing')" @click="selectedMenu = 'clothing'" :class="selectedMenu === 'clothing' ? 'clothing-button active' : 'clothing-button'">Tøj</button>
                    <button v-if="hasStoreItems('accessories')" @click="selectedMenu = 'accessories'" :class="selectedMenu === 'accessories' ? 'clothing-button active' : 'clothing-button'">Tilbehør</button>
                    <button v-if="hasWardrobe === true" @click="selectedMenu = 'wardrobe'" :class="selectedMenu === 'wardrobe' ? 'clothing-button active' : 'clothing-button'">Garderobe</button>
                </div>

                <div class="clothing-body" v-if="!isTattoShop">
                    <CameraMenu :NameResource="NameResource" :isTattoShop="isTattoShop" :selectedMenu="selectedMenu" :close="close"/>

                    <ModuleMenu v-if="isClothing()" :Components="Components" :NameResource="NameResource" :selectedMenu="selectedMenu" :selectedStore="selectedStore" :close="close"/>
                    <WardrobeMenu v-if="selectedMenu === 'wardrobe'" :NameResource="NameResource" :Wardrobe="Wardrobe" :selectedMenu="selectedMenu" :close="close"/>
                </div>

                <div class="tatto-body" v-if="isTattoShop">
                    <CameraMenu :NameResource="NameResource" :isTattoShop="isTattoShop" :selectedMenu="selectedMenu" :close="close"/>
                    <TattoModuleMenu :Tattos="Tattos" :PlayerJob="PlayerJob" :PlayerIdentifier="PlayerIdentifier" :NameResource="NameResource" :close="close"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import axios from 'axios';
import Swal from 'sweetalert2';

import CameraMenu from './screens/CameraMenu';
import ModuleMenu from './screens/ModuleMenu';
import WardrobeMenu from './screens/WardrobeMenu';
import TattoModuleMenu from './screens/TattoModuleMenu';

export default {
    components: { 
        CameraMenu, 
        ModuleMenu, 
        WardrobeMenu, 
        TattoModuleMenu, 
    },

    data: function(){
        return({
            opened: false,
            isFreeroaming: false,
            hasWardrobe: false,
            isTattoShop: false,
            selectedMenu: '',
            selectedStore: '',
            Components: [],
            Wardrobe: [],
            Tattos: [],
            PlayerJob: "none",
            PlayerIdentifier: "none",
            NameResource: "esx_clothing",
        })
    },

    methods: {
        receiveLua: function(event){
            if(!event || !event.data) return;
            const lua = event.data;

            if(lua.init) {
                this.PlayerJob = lua.PlayerJob;
                this.PlayerIdentifier = lua.Identifier;
                this.NameResource = lua.NameResource;
            }

            if(lua.open) {
                this.opened = true;
                this.Components = lua.Components;
                this.Wardrobe = lua.Wardrobe;

                this.setStarterItem();
            }

            if(lua.openStore) {
                this.Components = lua.Components;
                this.Wardrobe = lua.Wardrobe;
                this.selectedStore = lua.store;
                this.hasWardrobe = lua.hasWardrobe;

                this.opened = true;

                this.setStarterItem();
            }

            if(lua.openTatto) {
                this.Tattos = lua.Tattos;

                this.opened = true;
                this.isTattoShop = true;
            }

            if(lua.refreshComponents) {
                this.Components = lua.Components;
            }

            if(lua.refreshWardrobe) {
                this.Wardrobe = lua.Wardrobe;
            }

            if(lua.refreshPlayerJob) {
                this.PlayerJob = lua.PlayerJob;
            }

            if(lua.refreshTattos) {
                this.Tattos = lua.Tattos;
            }

            if(lua.updateTexture) {
                this.updateTextureMax(lua.name, lua.value);
            }

            if(lua.ToggleFreeRoam) {
                this.ToggleFreeRoam(false);
            }
        },

        SelectMenu: function(menu) {
            this.selectedMenu = menu;
        },

        isClothing: function() {
            return this.selectedMenu === 'body' || this.selectedMenu === 'clothing' || this.selectedMenu === 'accessories';
        },

        FindItem: function(name) {
            for (let key in this.Components) {
                let component = this.Components[key];

                if(component.name == name) {
                    return component;
                }
            }

            return null;
        },

        hasStoreItems: function(category) {
            let items = [];

            for (let key in this.Components) {
                let component = this.Components[key];

                if(category === component.category) {
                    if(this.selectedStore !== '') {
                        if(component.store !== this.selectedStore) continue;
                    }

                    items.push(component);
                }
            }

            return items.length > 0;
        },

        setStarterItem: function() {
            for (let key in this.Components) {
                let component = this.Components[key];

                if(this.hasStoreItems(component.category)) {
                    this.selectedMenu = component.category;
                    return
                }
            }
        },

        updateTextureMax: function(name, value) {
            let item = this.FindItem(name);

            if(item == null) return;
            if(item.texturemax == undefined) return;

            item.texturemax = value;
        },

        RotateCamera: function(direction) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/RotateCamera`, {
                direction: direction
            }).catch(function (error) {
                console.log(error + ' : Error at RotateCamera');
            });
        },

        ToggleFreeRoam: function(status) {
            this.isFreeroaming = status;

            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/ToggleFreeRoam`, {
                isFreeroaming: this.isFreeroaming
            }).catch(function (error) {
                console.log(error + ' : Error at ToggleFreeRoam');
            });
        },

        close: function() {     
            if(this.closed) return;

            if(!this.isTattoShop) {
                Swal.fire({
                    title: 'Vil gemme dit tøj?',
                    showDenyButton: true,
                    showCancelButton: true,
                    confirmButtonText: `Ja`,
                    cancelButtonText: `Annullere`,
                    denyButtonText: `Nej`,
                }).then((result) => {
                    if (result.isConfirmed) {
                        const NameResource = this.NameResource[0];
                        axios.post(`https://${NameResource}/saveClose`).catch(function (error) {
                            console.log(error + ' : Error at saveClose'); 
                        });

                        this.selectedStore = '';

                        this.opened = false;
                        this.hasWardrobe = false;
                    }

                    if (result.isDenied) {
                        const NameResource = this.NameResource[0];
                        axios.post(`https://${NameResource}/nosaveClose`).catch(function (error) {
                            console.log(error + ' : Error at nosaveClose'); 
                        });

                        this.selectedStore = '';

                        this.opened = false;
                        this.hasWardrobe = false;
                    }
                })
            }

            if(this.isTattoShop) {
                const NameResource = this.NameResource[0];
                axios.post(`https://${NameResource}/closeTattoShop`).catch(function (error) {
                    console.log(error + ' : Error at closeTattoShop'); 
                });

                this.opened = false;
                this.isTattoShop = false;
            }
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
* {
	padding: 0; 
	margin: 0; 
	box-sizing: border-box; 
    font-family: Roboto-Regular;
    overflow: hidden;
}

button:hover {
    cursor: pointer;
}

button:focus {
    outline: none;
}

.freeroam-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    animation: fadeIn .5s;
    -webkit-animation: fadeIn .5s;
    -moz-animation: fadeIn .5s;
    -o-animation: fadeIn .5s;
    -ms-animation: fadeIn .5s;
}

.freeroam-wrapper {
    width: 20em;
    margin-top: 4em;
    z-index: 100;
}

.freeroam-wrapper {
    width: 20em;
    margin-top: 4em;
    z-index: 100;
    background-color: #424242;
    padding: 8px;
    border-radius: 3px;
    text-align: center;
}

.freeroam-wrapper h1 {
    color: #e7e7e7;
    font-size: 20px;
    font-weight: bold;
}

.rotate-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    align-items: center;
    animation: fadeIn .5s;
    -webkit-animation: fadeIn .5s;
    -moz-animation: fadeIn .5s;
    -o-animation: fadeIn .5s;
    -ms-animation: fadeIn .5s;
}

.rotate-wrapper {
    width: 20em;
    margin-bottom: 4em;
    display: flex;
    justify-content: space-between;
    z-index: 100;
}

.rotate-wrapper button {
    flex: 2;
    height: 2em;
    background-color: #424242;
    color: #e7e7e7;
    border: none;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    font-size: 16px;
    font-weight: bold;
    margin: 5px 10px;
}

.rotate-wrapper button.middle {
    flex: 1;
}

.rotate-wrapper button:hover {
    background-color: #ae1e1e;
}

.clothing {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: column;
    align-items: flex-end;
    animation: fadeIn .5s;
    -webkit-animation: fadeIn .5s;
    -moz-animation: fadeIn .5s;
    -o-animation: fadeIn .5s;
    -ms-animation: fadeIn .5s;
}

.clothing-container {
    width: 500px;
    height: 100vh;
    padding-top: 25px;
    color: #e7e7e7;
    background-color: #2e2e2e;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

/* Clothing Main Menu */

.clothing-header {
    display: flex;
    padding-bottom: 25px;
}

.clothing-header .clothing-button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
    background-color: #575757;
    height: 50px;
}

.clothing-header .clothing-button.active, .clothing-header .clothing-button:hover {
    background-color: #ae1e1e;
}

.clothing-body, .tatto-body {
    display: flex;
    flex-flow: column;
    width: 100%;
    padding: 0 8px;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}
</style>