<template>
    <div class="camera-menu">
        <button v-if="!isTattoShop" @click="ToggleClothing('hat')" class="camera-button"><i class="fab fa-redhat"></i></button>
        <button v-if="!isTattoShop" @click="ToggleClothing('glasses')" class="camera-button"><i class="fas fa-glasses"></i></button>
        <button v-if="isTattoShop" @click="ToggleClothing('mask')" class="camera-button"><i class="fas fa-theater-masks"></i></button>
        <button v-if="isTattoShop" @click="ToggleClothing('pants')" class="camera-button"><i class="fas fa-socks"></i></button>
        <button @click="ToggleClothing('shirt')" class="camera-button"><i class="fas fa-tshirt"></i></button>
        <button @click="Handsup()" class="camera-button"><i class="fas fa-praying-hands"></i></button>
        <button @click="SetCamera('face')" class="camera-button"><i class="fas fa-smile-beam"></i></button>
        <button @click="SetCamera('normal')" class="camera-button"><i class="fas fa-user-tie"></i></button>
        <button @click="SetCamera('feet')" class="camera-button"><i class="fas fa-socks"></i></button>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    props: {
        isTattoShop: {},
        selectedMenu: {},
        NameResource: {},
        close: {},
    },

    methods: {
        SetCamera: function(type) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/SetCamera`, {
                type: type, 
                isTattoShop: this.isTattoShop, 
            }).catch(function (error) {
                console.log(error + ' : Error at SetCamera');
            });
        },

        ToggleClothing: function(type) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/ToggleClothing`, {
                type: type, 
            }).catch(function (error) {
                console.log(error + ' : Error at ToggleClothing');
            });
        },

        Handsup: function() {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/Handsup`).catch(function (error) {
                console.log(error + ' : Error at Handsup');
            });
        }
    }
}
</script>

<style>
.camera-menu {
    display: flex;
    padding-bottom: 25px;
}

.camera-menu .camera-button {
    flex: 1;
    padding: 5px;
    outline: none;
    border: none;
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 1.5em;
    font-weight: bold;
    background-color: #c32222;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: .5px;
}

.camera-menu .camera-button:hover {
    background-color: #ae1e1e;
}
</style>