<template>
    <div class="tatto-elements">
        <div v-for="(item, index) in SortedTattoList" :key="index" :class="index === (SortedTattoList.length - 1) ? 'tatto-element end' : 'tatto-element'">
            <div class="tatto-element-header">
                <h1>{{item.title}}</h1>
                <div class="header-buttons" v-if="item.current !== 0">
                    <button @click="RemoveTatto(item)" v-if="hasAcquiredNumber(item)">fjern</button>
                    <button @click="AddTatto(item)" class="green" v-if="!hasAcquiredNumber(item)">tilføj</button>
                </div>
            </div>
            <div class="tatto-element-body">
                <div class="tatto-item">
                    <h3>Maks Numre: {{item.qty}}</h3>
                    <div class="tatto-buttons">
                        <button @click="removeTattoItemValue(item)"><i class="fas fa-chevron-left"></i></button>
                        <input type="number" v-model="item.current" @change="onChangeTattoItemValue(item)"/>
                        <button @click="addTattoItemValue(item)"><i class="fas fa-chevron-right"></i></button>
                    </div>
                </div>
            </div>
        </div>

        <div class="reset-button">
            <button @click="ResetAllTattos()">RESET ALT</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';

export default {
    props: {
        Tattos: {},
        PlayerJob: {},
        PlayerIdentifier: {},
        NameResource: {},
        close: {},
    },

    methods: {
        onChangeTattoItemValue: function(item) {
            if(item.qty <= item.current) item.current = item.qty;
            if(0 >= item.current) item.current = 0;

            this.SetTattoItem(item);
        },

        removeTattoItemValue: function(item) {
            if(0 >= item.current) return
            item.current--;

            this.SetTattoItem(item);
        },

        addTattoItemValue: function(item) {
            if(item.qty <= item.current) return
            item.current++;

            this.SetTattoItem(item);
        },

        SetTattoItem: function(item) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/SetTatto`, {
                item: item
            }).catch(function (error) {
                console.log(error + ' : Error at SetTatto');
            });
        },

        hasAcquiredNumber: function(item) {
            for (let key in item.acquired) {
                let acquired = item.acquired[key];

                if(item.current === acquired) return true;
            } 

            return false;
        },

        RemoveTatto: function(item) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/RemoveTatto`, {
                item: item
            }).catch(function (error) {
                console.log(error + ' : Error at RemoveTatto');
            });
        },

        AddTatto: function(item) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/AddTatto`, {
                item: item
            }).catch(function (error) {
                console.log(error + ' : Error at AddTatto');
            });
        },

        ResetAllTattos: function() {
            Swal.fire({
                title: 'Vil reset alle Tattoveringer?',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Annullere`,
            }).then((result) => {
                if (result.isConfirmed) {
                    const NameResource = this.NameResource[0];
                    axios.post(`https://${NameResource}/ResetAllTattos`).catch(function (error) {
                        console.log(error + ' : Error at ResetAllTattos'); 
                    });
                }
            })
        },
    },

    computed: {
        SortedTattoList() {
            let sortedList = [];

            for (let key in this.Tattos) {
                let Tatto = this.Tattos[key];

                let hasJob = true;
                if(Tatto.requiredJobs !== undefined) {
                    if(Tatto.requiredJobs.length > 0) {
                        hasJob = false;

                        for (let i = 0; i < Tatto.requiredJobs.length; i++) {
                            let job = Tatto.requiredJobs[i];

                            if (job === this.PlayerJob) {
                                hasJob = true;
                            }
                        }
                    }
                }

                let isWhitelisted = true;
                if(Tatto.whitelistedPlayers !== undefined) {
                    if(Tatto.whitelistedPlayers.length > 0) {
                        isWhitelisted = false;

                        for (let i = 0; i < Tatto.whitelistedPlayers.length; i++) {
                            let player = Tatto.whitelistedPlayers[i];

                            if (player === this.PlayerIdentifier) {
                                isWhitelisted = true;
                            }
                        }
                    }
                }
                
                if (hasJob && isWhitelisted) {
                    sortedList.push(Tatto);
                }
            }

            return sortedList;
        },
    },
}
</script>

<style>
.tatto-elements {
    display: block;
    max-height: calc(100vh - 100px);
    overflow: auto;
}

.tatto-elements::-webkit-scrollbar {
    width: 0.5em;
}

.tatto-elements::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.tatto-elements::-webkit-scrollbar-thumb {
    background-color: #ae1e1e;
    outline: 1px solid #313131;
    border-radius: 5px;
}

.tatto-elements input:focus {
    outline: none;
}

.tatto-elements input[type=number]::-webkit-inner-spin-button,
.tatto-elements input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
}

.tatto-elements input[type=number] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-shadow: none !important;
}

.tatto-element {
    margin: 0px 10px 20px 10px;
    border-radius: 5px;
}

.tatto-element.end {
    margin-bottom: 0;
}

.tatto-element-header {
    height: 45px;
    background-color: #424242;
    padding: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tatto-element-header button {
    width: 100px;
    background-color: #c32222;
    color: #e7e7e7;
    border: none;
    border-radius: 3px;
    text-transform: uppercase;
    padding: 4px;
    font-size: 16px;
    font-weight: bold;
}

.tatto-element-header button.green {
    background-color: #4CAF50;
}

.tatto-element-header h1 {
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 20px;
}

.tatto-element-body {
    display: flex;
    background-color: #575757;
    padding: 8px 0;
}

.tatto-element-body .tatto-item {
    flex: 1;
    display: flex;
    flex-flow: column;
}

.tatto-element-body .tatto-item h3 {
    color: #e7e7e7;
    margin: 3px 10px;
    font-size: 16px;
}

.tatto-element-body .tatto-buttons {
    width: 100%;
    display: flex;
    margin: 5px;
}

.tatto-element-body .tatto-item button {
    flex: 1;
    background-color: #424242;
    color: #e7e7e7;
    border: none;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}

.tatto-element-body .tatto-item button:hover {
    background-color: #ae1e1e;
}

.tatto-element-body .tatto-item input {
    flex: 2;
    color: #e7e7e7;
    background: none;
    border: none;
    border-bottom: 1px solid #e7e7e7;
    margin: 0 5px;
    text-align: center;
}

.tatto-element-body .tatto-item.has-textures input {
    flex: 3;
}

.tatto-element-body .tatto-item button,
.tatto-element-body .tatto-item input {
    color: #e7e7e7;
    height: 30px;
    font-size: 16px;
    font-weight: bold;
}

.reset-button {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
}

.reset-button button {
    width: 450px;
    height: 35px;
    outline: none;
    border: none;
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: bold;
    background-color: #c32222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: .5px;
    border-radius: 3px;
}

.reset-button button:hover {
    background-color: #ae1e1e;
}
</style>