<template>
    <div class="elements">
        <div v-for="(item, index) in SortedSkinList" :key="index" :class="index === (SortedSkinList.length - 1) ? 'element end' : 'element'">
            <div class="element-header">
                <h1>{{item.label}}</h1>
            </div>
            <div class="element-body">
                <div :class="hasItemTextures(item) ? 'element-item has-textures' : 'element-item'">
                    <h3>Maks Numre: {{item.max}}</h3>
                    <div class="element-buttons">
                        <button @click="removeItemValue(item, false)"><i class="fas fa-chevron-left"></i></button>
                        <input type="number" v-model="item.value" @change="onChangeItemValue(item, false)"/>
                        <button @click="addItemValue(item, false)"><i class="fas fa-chevron-right"></i></button>
                    </div>
                </div>
                <div v-if="hasItemTextures(item)" :class="hasItemTextures(item) ? 'element-item has-textures' : 'element-item'">
                    <h3>Maks Textures: {{item.texturemax}}</h3>
                    <div class="element-buttons">
                        <button @click="removeItemValue(item, true)"><i class="fas fa-chevron-left"></i></button>
                        <input type="number" v-model="item.texturevalue" @change="onChangeItemValue(item, true)"/>
                        <button @click="addItemValue(item, true)"><i class="fas fa-chevron-right"></i></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    props: {
        Components: {},
        selectedStore: {},
        selectedMenu: {},
        NameResource: {},
        close: {},
    },

    methods: {
        hasItemTextures: function(item) {
            return item.texturename !== undefined && item.texturename !== '';
        },

        onChangeItemValue: function(item, isTexture) {
            if(isTexture == false) {
                if(item.max <= item.value) item.value = item.max;
                if(item.min >= item.value) item.value = item.min;

                if(this.hasItemTextures(item)) {
                    item.texturevalue = 0;
                    this.SetClothingItem(item.texturename, item.texturevalue);
                }

                this.SetClothingItem(item.name, item.value);
            }

            if(isTexture == true) {
                if(item.texturemax <= item.texturevalue) item.texturevalue = item.texturemax;
                if(item.texturemin >= item.texturevalue) item.texturevalue = item.texturemin;

                this.SetClothingItem(item.texturename, item.texturevalue);
            }
        },

        addItemValue: function(item, isTexture) {
            if(isTexture == false) {
                if(item.max <= item.value) return
                item.value++;

                if(this.hasItemTextures(item)) {
                    item.texturevalue = 0;
                    this.SetClothingItem(item.texturename, item.texturevalue);
                }

                this.SetClothingItem(item.name, item.value);
            }

            if(isTexture == true) {
                if(item.texturemax <= item.texturevalue) return
                item.texturevalue++;

                this.SetClothingItem(item.texturename, item.texturevalue);
            }
        },

        removeItemValue: function(item, isTexture) {
            if(isTexture == false) {
                if(item.min >= item.value) return
                item.value--;

                if(this.hasItemTextures(item)) {
                    item.texturevalue = 0;
                    this.SetClothingItem(item.texturename, item.texturevalue);
                }

                this.SetClothingItem(item.name, item.value);
            }

            if(isTexture == true) {
                if(item.texturemin >= item.texturevalue) return
                item.texturevalue--;

                this.SetClothingItem(item.texturename, item.texturevalue);
            }
        },

        SetClothingItem: function(name, value) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/SetClothing`, {
                name: name, 
                value: value, 
            }).catch(function (error) {
                console.log(error + ' : Error at SetClothing');
            });
        }
    },

    computed: {
        SortedSkinList() {
            let sortedList = [];

            for (let key in this.Components) {
                let component = this.Components[key];

                if(component.category === this.selectedMenu) {
                    if(this.selectedStore !== '') {
                        if(component.store !== this.selectedStore) continue;
                    }

                    sortedList.push(component);
                }
            }

            return sortedList;
        },
    },
}
</script>

<style>
.elements {
    display: block;
    max-height: calc(100vh - 200px);
    overflow: auto;
}

.elements::-webkit-scrollbar {
    width: 0.5em;
}

.elements::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.elements::-webkit-scrollbar-thumb {
  background-color: #ae1e1e;
  outline: 1px solid #313131;
  border-radius: 5px;
}

.elements input:focus {
    outline: none;
}

.elements input[type=number]::-webkit-inner-spin-button,
.elements input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
}

.elements input[type=number] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-shadow: none !important;
}

.element {
    margin: 0px 10px 20px 10px;
    border-radius: 5px;
}

.element.end {
    margin-bottom: 0;
}

.element-header {
    background-color: #424242;
    padding: 8px;
}

.element-header h1 {
    color: #e7e7e7;
    text-transform: uppercase;
    font-size: 20px;
}

.element-body {
    display: flex;
    background-color: #575757;
    padding: 8px 0;
}

.element-body .element-item {
    flex: 1;
    display: flex;
    flex-flow: column;
}

.element-body .element-item h3 {
    color: #e7e7e7;
    margin: 3px 10px;
    font-size: 16px;
}

.element-body .element-buttons {
    width: 100%;
    display: flex;
    margin: 5px;
}

.element-body .element-item button {
    flex: 1;
    background-color: #424242;
    color: #e7e7e7;
    border: none;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}

.element-body .element-item button:hover {
    background-color: #ae1e1e;
}

.element-body .element-item input {
    flex: 2;
    color: #e7e7e7;
    background: none;
    border: none;
    border-bottom: 1px solid #e7e7e7;
    margin: 0 5px;
    text-align: center;
}

.element-body .element-item.has-textures input {
    flex: 3;
}

.element-body .element-item button,
.element-body .element-item input {
    color: #e7e7e7;
    height: 30px;
    font-size: 16px;
    font-weight: bold;
}
</style>